.container_block {
    width: 1392px;
    height: 571px;
    gap: 16px;
    margin: 64px 0 0 0;
    display: flex;
    justify-content: space-between;
}

.container_image {
    width: 571px;
    height: 100%;
    background-image: url('../../../assets/img/work_img/work_07.png');
    background-size: cover;
    background-position: center;
    background-repeat: no-repeat;
}

.container_about_block {
    width: 805px;
    height: 100%;
    border-radius: 24px;
    padding: 32px;
    background-color: white;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
}

.container_about_block::-webkit-scrollbar {
    width: 0;
}

.container_btn {
    font-size: 14px;
    line-height: 16px;
    width: 48px;
    height: 32px;
    border-radius: 28px;
    background-color: white;
    border: 1px solid;
}

.container_text_block {
    width: 741px;
    height: auto;
}

.container_name {
    font-size: clamp(31px, 1.483vw + 25.215px, 48px);
    line-height: 60px;
    letter-spacing: 1%;
    font-family: "Plus Jakarta Sans", sans-serif;
    font-optical-sizing: auto;
    font-weight: 400;
    font-style: normal;
}

.container_text {
    font-family: "Plus Jakarta Sans", sans-serif;
    font-optical-sizing: auto;
    font-weight: 400;
    font-style: normal;
    font-size: clamp(16px, 0.349vw + 14.639px, 20px);
    line-height: 32px;
    letter-spacing: 1%;
    margin-top: 16px;
}


@media screen and (max-width: 480px) {
    .container_block {
        width: 100%;
        height: 863px;
        margin: 48px 0 0 0;
        border-radius: 24px;
        gap: 16px;
        display: flex;
        flex-direction: column;
        justify-content: space-between;
    }
    
    .container_about_block {
        width: 100%;
        height: 489px;
        padding: 24px;
        gap: 80px;
        border-radius: 24px;
        background-color: white;
        display: flex;
        flex-direction: column;
        justify-content: space-between;
        border: 1px solid #E1E1E1;
        overflow: scroll;
    }

    .container_text_block {
        width: 100%;
        height: 329px;
        gap: 20px;
    }
    
    .container_name {
        line-height: 37.2px;
        letter-spacing: 1%;
