.container_block {
  width: 1392px;
  height: auto;
  display: flex;
  justify-content: space-between;
  flex-wrap: wrap;
}

.project_block {
  width: 1392px;
  height: 688px;
  margin-top: 16px;
  border-radius: 24px;
  border: 1px solid #b7b7b7;
  filter: brightness(0.8);
  background-size: cover;
  background-repeat: no-repeat;
  position: relative;
}

.project_btn {
  width: 106px;
  height: 32px;
  background-color: #2e2e2e66;
  border: 0.5px solid #b7b7b7;
  color: white;
  border-radius: 28px;
  gap: 10px;
  position: absolute;
  top: 32px;
  right: 32px;
}

.project_name {
  color: white;
  font-family: "Plus Jakarta Sans", sans-serif;
  font-optical-sizing: auto;
  font-weight: 600;
  font-style: normal;
  font-size: clamp(1.5rem, 0.9895rem + 2.0942vw, 3rem);
  line-height: clamp(1.8rem, 1.1364rem + 2.7225vw, 3.75rem);
  position: absolute;
  bottom: 130px;
  left: 32px;
}

.project_text {
  width: 70%;
  color: white;
  font-family: "Plus Jakarta Sans", sans-serif;
  font-optical-sizing: auto;
  font-weight: 400;
  font-style: normal;
  font-size: clamp(1rem, 0.8298rem + 0.6981vw, 1.5rem);
  position: absolute;
  bottom: 32px;
  left: 32px;
}

.container_read {
  width: 923px;
  height: fit-content;
  margin-top: 64px;
  padding: 32px;
  border-radius: 24px;
  background-color: #ffffff;
  border: 1px solid #e1e1e1;
  display: flex;
  flex-direction: column;
  gap: 24px;
}

.read_title_box {
  width: 100%;
  height: 100%;
  display: flex;
  flex-direction: column;
  gap: 24px;
}

.read_btn {
  width: fit-content;
  height: 32px;
  padding: 0 16px;
  border-radius: 28px;
  background-color: inherit;
  border: 1px solid #2e2e2e;
  font-size: 14px;
  line-height: 16px;
  font-family: "Plus Jakarta Sans", sans-serif;
  font-optical-sizing: auto;
  font-weight: 600;
  font-style: normal;
}

.interier_text {
  font-family: "Plus Jakarta Sans", sans-serif;
  font-optical-sizing: auto;
  font-weight: 400;
  font-style: normal;
  font-size: 20px;
  line-height: 32px;
  color: #2e2e2e;
}

.info_boxes {
  width: 100%;
  height: auto;
  display: flex;
  flex-direction: column;
  gap: 16px;
}
.info_name {
  font-family: "Plus Jakarta Sans", sans-serif;
  font-optical-sizing: auto;
  font-weight: 600;
  font-style: normal;
  font-size: 24px;
  line-height: 32px;
  color: #2e2e2e;
}
.info_img_box {
  width: 100%;
  height: auto;
  display: flex;
  justify-content: space-between;
  flex-wrap: wrap;
  gap: 16px 0;
}
.info_img_box > img {
  height: 400px;
}

.info_title {
  font-size: 16px;
  line-height: 26px;
  font-family: "Plus Jakarta Sans", sans-serif;
  font-optical-sizing: auto;
  font-weight: 400;
  font-style: normal;
}

.info_description {
  font-size: 16px;
  line-height: 26px;
  font-family: "Plus Jakarta Sans", sans-serif;
  font-optical-sizing: auto;
  font-weight: 400;
  font-style: normal;
}

.read_pagination {
  width: 100%;
  height: 56px;
  display: flex;
  justify-content: space-between;
}
.read_pagination > button {
  width: 421.5px;
  height: 100%;
  border-radius: 32px;
  border: 1px solid #2e2e2e;
  font-size: 16px;
  line-height: 16px;
  display: flex;
  justify-content: center;
  align-items: center;
  gap: 0 2px;
}
.read_pagination > button:hover {
  border: none;
  background-color: #764af1;
  color: white;
  cursor: pointer;
}
.read_icon {
  width: 20px;
  height: 20px;
}

.right_block {
  width: 453px;
  height: auto;
  margin-top: 64px;
  display: flex;
  flex-direction: column;
  gap: 16px;
}

.popular_block {
  width: 100%;
  height: auto;
  border-radius: 24px;
  padding: 16px;
  display: flex;
  flex-direction: column;
  gap: 16px;
  background-color: #e1e1e1;
